.section {
  width: 100%;
  padding-top: 60px;
  padding-bottom: 60px;
}

.grid-1-parent {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.grid-1-child-1 {
  grid-area: 2 / 1 / 3 / 2;
}
.grid-1-child-2 {
  grid-area: 1 / 2 / 4 / 3;
}

.grid-2-parent {
  display: grid;
  grid-template-columns: 1fr repeat(2, 2fr) 1fr;
  grid-template-rows: repeat(4, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 20px;
  justify-items: center;
}

.card-style {
  border-radius: 15px;
  padding: 30px;
  width: 100%;
  -webkit-box-shadow: 0px 5px 29px -2px rgba(0, 0, 0, 0.33);
  box-shadow: 0px 5px 29px -2px rgba(0, 0, 0, 0.33);
}

.grid-2-child-1 {
  grid-area: 1 / 2 / 2 / 4;
}
.grid-2-child-2 {
  grid-area: 2 / 2 / 3 / 3;
}
.grid-2-child-3 {
  grid-area: 2 / 3 / 3 / 4;
}
.grid-2-child-4 {
  grid-area: 3 / 2 / 4 / 3;
}
.grid-2-child-5 {
  grid-area: 3 / 3 / 4 / 4;
}
.grid-2-child-6 {
  grid-area: 4 / 2 / 5 / 3;
}
.grid-2-child-7 {
  grid-area: 4 / 3 / 5 / 4;
}

.phone-image-container {
  height: 550px;
}
.phone-image-container img {
  max-width: 100%;
  max-height: 100%;
  padding-left: 40px;
}

.blue-gradient {
  background: rgb(6, 0, 99);
  background: linear-gradient(
    90deg,
    rgba(6, 0, 99, 1) 6%,
    rgba(0, 69, 255, 1) 100%
  );
}

.washed-blue-gradient {
  background: rgb(0, 48, 115);
  background: linear-gradient(
    153deg,
    rgba(0, 48, 115, 1) 13%,
    rgba(173, 163, 232, 1) 100%
  );
}

.washed-blue-purple-gradient {
  background: rgb(119, 173, 249);
  background: linear-gradient(
    153deg,
    rgba(119, 173, 249, 1) 13%,
    rgba(225, 49, 244, 1) 100%
  );
}

.washed-red-yellow-gradient {
  background: rgb(249, 119, 119);
  background: linear-gradient(
    153deg,
    rgba(249, 119, 119, 1) 13%,
    rgba(244, 196, 49, 1) 100%
  );
}
